let LAYER_WMS = new ol.layer.Tile({
  title: "LAYER_WMS",
        	source: new ol.source.TileWMS({
	            url: 'https://d2map.com/geoserver/D2Map/wms',
	            params: {
              	  LAYERS: 'afa001,sudo',
	                VERSION: '1.1.0',
              	  SRS: 'EPSG:4326',
	                TRANSPARENT: true,
	            },
            	serverType: 'geoserver',
            	crossOrigin: 'Anonymous', //미 선언시 Export(png, jpg) 오류 발생
	        }),
          opacity: 1.0,
          visible:true
	 });
map.addLayer(LAYER_WMS);